<template>
  <div>
    <Header></Header>
    <br>
    <v-card class="qs" max-width="1200">
      <v-card-actions>
        <v-btn style="margin-inline: 1%" @click="showSeq='true'">显示序号</v-btn>
        <v-btn style="margin-inline: 1%" @click="showSeq='false'">隐藏序号</v-btn>
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-btn
                v-bind="attrs"
                v-on="on"
                style="margin-inline: 1%" @click="overlay = !overlay"
            >
              问卷描述
            </v-btn>
          </template>
          <span>编辑您的问卷描述</span>
        </v-tooltip>
        <v-btn style="background-color:skyblue;color:white;" @click="jumpToCreate">创建面板</v-btn>
        <v-overlay :value="overlay">
          <v-card style="margin: 0 auto; width:600px; height: 250px;" class="primary">
            <v-card-title class="text-h6" background-color="lightsteelblue">
              请输入问卷描述
            </v-card-title>
            <v-card-text style="margin-top:2em; text-align:center;width:500px;" class="primary">
              <v-text-field label="描述" dense style="width: 30vw; margin-left: 0vw; font-size:20px;"
                            v-model="paperDescription"  color="white"
              ></v-text-field>
            </v-card-text>
            <v-card-actions>
              <v-btn style="margin-inline:1%" color="warning" @click="overlay = false">确定</v-btn>
              <v-btn color="warning" @click="canceldis">取消</v-btn>
            </v-card-actions>
          </v-card>
        </v-overlay>
        <v-spacer></v-spacer>
        <div style="font-size:30px;margin-right:50%;font-family:'bgtitlefamily';"> 报名问卷 </div>
      </v-card-actions>
    </v-card>
    <br>
    <v-dialog    v-model="desdialog"  width="500" height="400">
      <v-card>
        <v-card-title class="text-h5 primary lighten-2">
          请输入问卷描述
        </v-card-title>
        <v-card-text>
            <v-textarea label="请输入问卷描述"
      style="width: 30vw; margin-left: 0vw; margin-top: 5vh"
       v-model="paperDescription"
    ></v-textarea>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary"  text  @click="desdialog=false">
            确认
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog    v-model="tip"  width="500" height="400">
      <v-card>
        <v-card-title class="text-h5 primary lighten-2">
          提示
        </v-card-title>
        <v-card-text>
            <h3 style="margin-top:1vh;">确认是否要删除草稿</h3>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary"  text  @click="deleteConfirm">
            确认
          </v-btn>
          <v-btn color="primary"  text  @click="deleteCancel">
            取消
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="isEdit" width="600" height="400" v-if="isEdit">
      <v-card>
        <v-card-title class="text-h5 primary lighten-2">
          编辑{{editQuestion.type}}
        </v-card-title>
        <v-card-text>
          <h4 style="margin-left:2vw;margin-top:1.5vh;">修改题目为</h4>
          <v-text-field  style="width:20vw;margin-left:2vw;margin-top:1vh;" dense
            v-model="editQuestion.title" solo :label="editQuestion.title"
        ></v-text-field>
          <h4 style="margin-left:2vw;margin-top:1vh;">是否必填</h4>
          <v-radio-group style="margin-left:2vw;" row
            v-model="editQuestion.necessary"
        >
          <v-radio  label="是" value="true"></v-radio>
          <v-radio  label="否" value="false"></v-radio>
        </v-radio-group>
        <h4 style="margin-left:2vw;margin-top:1.5vh;">题目描述</h4>
          <v-text-field  style="width:20vw;margin-left:2vw;margin-top:1vh;" dense
            v-model="editQuestion.description" solo :label="editQuestion.description"
        ></v-text-field>
        <h4 style="margin-left:2vw;margin-top:1vh;" v-if="editQuestion.type=='单选报名题'||editQuestion.type=='多选报名题'">是否展示结果</h4>
          <v-radio-group style="margin-left:2vw;" row
            v-model="editQuestion.ptype" v-if="editQuestion.type=='单选报名题'||editQuestion.type=='多选报名题'"
        >
          <v-radio  label="是" value="0"></v-radio>
          <v-radio  label="否" value="-1"></v-radio>
        </v-radio-group>
        <div v-if="editQuestion.type=='多选题'||editQuestion.type=='多选报名题'">
        <h4 style="margin-left:2vw;margin-top:1vh;" >最多可选</h4>
        <v-select style="margin-left:2vw;margin-top:1vh;width:10vw;"
          :items="limits"
          v-model="editQuestion.limit"
          label="选择最多可选数量"
        ></v-select>
        </div>
        <h4 style="margin-left:2vw;margin-top:1vh;" v-if="this.question[this.editIndex].choices[0]!='无'">修改选项为</h4>
        <div v-if="this.question[this.editIndex].choices[0]!='无'">
        <div
          v-for="(item, cindex) in editQuestion.choices"
          :key="cindex"
          style="width: 22vw; margin-left: 2vw; "
        >
        <v-row style=" margin-top: 1vh; width:30vw;">
          <v-text-field
            label="输入选项"
            solo
            dense
            style="width: 15vw;  margin: 0 0 0 1vw"
            v-model="editQuestion.choices[cindex]"
          ></v-text-field>
          <v-text-field v-if="editQuestion.type=='单选报名题'||editQuestion.type=='多选报名题'"
            label="输入选项"
            solo
            dense
            style="width: 2vw;  margin: 0 0 0 1vw"
            v-model="editQuestion.numLimit[cindex]"
          ></v-text-field>
          <v-btn
            fab depressed dark
            x-small v-if="cindex==editQuestion.choices.length-1"
            color="success"
            style="
            float:right;
              margin: 1.2vh 0 0 1vw;
              height: 1.2vw;
              width: 1.2vw;
            "
            @click="addChoice(cindex)"
            ><v-icon dark>mdi-plus</v-icon></v-btn
          >
          <v-btn
            fab depressed dark
            x-small
            color="error"
            style="float:right;
              margin: 1.2vh 0 0 1vw;
              height: 1.2vw;
              width: 1.2vw;
            "
            @click="deleteChoice(cindex)"
            ><v-icon dark>mdi-close</v-icon></v-btn
          >
          </v-row>
        </div>
        </div>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn color="success" style="margin-left:18vw;" @click="confirmEdit">确认</v-btn>
          <v-btn color="warning" style="margin-left:2vw;" @click="cancelEdit">取消</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <div style="width: 1200px" class="qs">
      <v-card class="leftpart">
        <v-card-text>题型选择</v-card-text>
        <LeftBar :titleList="titleList" v-model="choices"></LeftBar>
        <v-card-text>问卷标题</v-card-text>
        <v-text-field v-model="title"  outlined dense style="margin-left:1.5vw;width:12vw;"
        ></v-text-field>
        <v-card-text>问卷是否只能填写一次</v-card-text>
        <v-checkbox
            v-model="isOneTime"
            :label="getIsOneTime"
            style="margin-left:1.5vw;width:12vw;"
        ></v-checkbox>
        <v-card-text>结束时间选择</v-card-text>
        <v-menu
            v-model="menu"
            :close-on-content-click="false"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                v-model="date"
                append-icon="mdi-calendar"
                readonly
                v-bind="attrs"
                v-on="on"
                outlined
                dense
                background-color="white"
                style="width: 12vw; margin-left: 10%"
              ></v-text-field>
            </template>
            <v-date-picker :allowed-dates="allowedDates"
            v-model="date" @input="menu = false"></v-date-picker>
          </v-menu>
          <v-menu
        ref="menu"
        :close-on-content-click="false"
        :nudge-right="40"
        :return-value.sync="time"
        offset-y
        max-width="290px"
        min-width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="time"
            append-icon="mdi-clock-time-four-outline"
            readonly
            v-bind="attrs"
            v-on="on"
            outlined
            background-color="white"
            dense
            style="width: 12vw; margin-left: 10%"
          ></v-text-field>
        </template>
        <v-time-picker
          v-model="time"
          full-width
          @click:minute="getTime"
        ></v-time-picker>
      </v-menu>
      </v-card>
      <v-card class="rigthpart" style="margin-bottom:20vh;padding-bottom:5vh;">
        <v-card-text style="text-align: center; font-size:25px">
          <v-btn color="success" style="margin-right: 5vw;" @click="issue">发布问卷</v-btn>
          <v-btn color="primary" style="margin-right: 5vw;" @click="save">保存问卷</v-btn>
          <v-btn color="warning" style="margin-right: 5vw;" @click="preview">预览问卷</v-btn>
          <v-btn color="error" style="margin-right: 2vw;" @click="deletePaper">删除草稿</v-btn>
        </v-card-text>
        <v-card v-for="(item,i) in question" :key="item.id"
        style="margin-left:1vw;margin-right:1vw;margin-top:3vh;" elevation="2">
          <v-row>
          <span style="margin-left:5vw;margin-right:0.5vw;margin-top:2vh;" ><span v-show="getIsShow">{{i+1}}.</span></span>
          <FillBlank v-model="question[i]" v-if="item.type=='填空题'" style="margin-top:2vh;"></FillBlank>
          <FillText v-model="question[i]" v-if="item.type=='文本题'" style="margin-top:2vh;"></FillText>
          <VoteRadio v-model="question[i]" style="margin-top:2vh;"
          v-if="item.type=='单选题'||item.type=='单选报名题'" :ptype="item.ptype"></VoteRadio>
          <VoteCheck v-model="question[i]" style="margin-top:2vh;"
          v-if="item.type=='多选题'||item.type=='多选报名题'" :ptype="item.ptype"></VoteCheck>
          <Select v-model="question[i]" style="margin-top:2vh;"
          v-if="item.type=='下拉框'" ></Select>
          <div v-if="item.type=='时间选择器'" style="width:55vh;margin-top:2vh;">
            <h4 style="margin-bottom:0.5vh;">
          <span style="color:red;" v-if="question[i].necessary==true">* </span>
          {{question[i].title}}</h4>
          <p>{{ question[i].description}}</p>
              <v-text-field
                append-icon="mdi-calendar"
                readonly
                outlined disabled
                dense
                background-color="white"
                style="width: 12vw; margin-left: 0"
              ></v-text-field>
          <v-text-field
            append-icon="mdi-clock-time-four-outline"
            readonly
          disabled
            outlined
            background-color="white"
            dense
            style="width: 12vw;float:right;margin-top:-9vh;"
          ></v-text-field>
          </div>
          </v-row>
          <v-card-actions style="margin-left:35vw;margin-top:1.5vh;" >
          <v-btn  class="mx-2" fab dark color="cyan" x-small depressed @click="edit(i)">
            <v-icon dark>
              mdi-pencil
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="red" x-small depressed  @click="close(i)">
            <v-icon dark>
              mdi-close-thick
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="green" x-small depressed  @click="copy(i)">
            <v-icon dark>
              mdi-file-document-multiple
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="warning" x-small depressed @click="up(i)">
            <v-icon dark>
              mdi-arrow-up
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="warning" x-small depressed  @click="down(i)">
            <v-icon dark>
              mdi-arrow-down
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="warning" x-small depressed  @click="upp(i)">
            <v-icon dark>
              mdi-format-vertical-align-top
            </v-icon>
          </v-btn>
          <v-btn  class="mx-2" fab dark color="warning" x-small depressed  @click="downn(i)">
            <v-icon dark>
              mdi-format-vertical-align-bottom
            </v-icon>
          </v-btn>
          </v-card-actions>
        </v-card>
      </v-card>
    </div>
  </div>
</template>

<script>
import "@/assets/font/font.css"
import QuestionnareCard from '@/components/QuestionnareCard'
import LeftBar from "@/components/Vote/LeftBar";
import FillBlank from "@/components/FillBlank";
import Header from "@/components/Header"
import FillText from "@/components/FillText";
import VoteRadio from "@/components/Vote/VoteRadio";
import VoteCheck from "@/components/Vote/VoteCheck";
import Select from "@/components/SignUp/Select";
var swapItems = function(arr, index1, index2,direction) {
  if(direction=='up'){//置顶
    arr.unshift(arr[index1]);
    arr.splice(index1+1,1);
    return arr;
  }
  if(direction=='down'){//置底
    arr.push(arr[index1]);
    arr.splice(index1,1);
    return arr;
  }
  arr[index1] = arr.splice(index2, 1, arr[index1])[0];
  return arr;
};
export default {
  name: "SignUpQuestionnare",
  components:{
    QuestionnareCard,
    LeftBar,FillText,
    FillBlank,VoteRadio,VoteCheck,
    Select,Header
  },
  computed:{
    getIsOneTime(){
      if(this.isOneTime === true){
        return "是";
      }
      else{
        return "否"
      }
    },
    getIsShow(){
      if(this.showSeq === 'false'){
        return false;
      }
      else{
        return true;
      }
    }
  },
  watch:{
    time:function (val, oldVal){
      this.endTime=this.date+' '+this.time+":00"
    },
    date:function (val, oldVal){
      this.endTime=this.date+' '+this.time+":00"
    },
    choices:function (val, oldVal) {
        if(val.indexOf('姓名')!=-1) {
          this.question.push({description:'',isSignup:false,ptype:-1,id:this.index,title:'您的姓名',answer:'',limit:-1,numLimit:['无'],choices:['无'],necessary:true,type:'填空题'})
          this.index=this.index+1
        }
        else if(val.indexOf('电话')!=-1) {
          this.question.push( {description:'',isSignup:false,ptype:-1,id:this.index,title:'您的电话',answer:'',limit:-1,numLimit:['无'],choices:['无'],necessary:false,type:'填空题'})
          this.index=this.index+1
        }
        else if(val.indexOf('个人简介')!=-1) {
          this.question.push( {description:'',isSignup:false,ptype:-1,id:this.index,title:'个人简介',answer:'',limit:-1,numLimit:['无'],choices:['无'],necessary:false,type:'文本题'})
          this.index=this.index+1
        }
        else if(val.indexOf('报名理由')!=-1) {
          this.question.push( {description:'',isSignup:false,ptype:-1,id:this.index,title:'报名理由',answer:'',limit:-1,numLimit:['无'],choices:['无'],necessary:false,type:'文本题'})
          this.index=this.index+1
        }
        else if(val.indexOf('性别')!=-1) {
          this.question.push( {description:'',isSignup:false,ptype:-1,id:this.index,title:'您的性别',answer:'',limit:-1,numLimit:['无'],choices:['男','女'],necessary:false,type:'单选题'})
          this.index=this.index+1
        }
        else if(val.indexOf('填空题')!=-1) {
          this.question.push({description:'',isSignup:false,id:this.index,title:'填空题',limit:-1,answer:'',numLimit:['无'],choices:['无'],necessary:false,type:'填空题'})
          this.index=this.index+1
        }
        else if(val.indexOf('文本题')!=-1) {
          this.question.push({description:'',isSignup:false,ptype:-1,id:this.index,title:'文本题',limit:-1,answer:'',numLimit:['无'],choices:['无'],necessary:false,type:'文本题'})
          this.index=this.index+1
        }
        else if(val.indexOf('单选题')!=-1){
          this.question.push({description:'',isSignup:false,ptype:-1,id:this.index,title:'单选',answer:'',limit:-1,numLimit:['无'],choices:['A','B','C','D'],necessary:false,type:'单选题'})
          this.index=this.index+1
        }
        else if(val.indexOf('单选报名题')!=-1){
          this.question.push({description:'',isSignup:true,ptype:0,id:this.index,title:'单选报名',answer:'',limit:-1,numLimit:['1','2','3','4'],choices:['A','B','C','D'],necessary:false,type:'单选报名题'})
          this.index=this.index+1
        }
        else if(val.indexOf('多选题')!=-1){
          this.question.push({description:'',isSignup:false,ptype:-1,id:this.index,title:'多选',answer:'',Answer:[],limit:2,numLimit:['无'],choices:['A','B','C','D'],necessary:false,type:'多选题'})
          this.index=this.index+1
        }
        else if(val.indexOf('多选报名题')!=-1){
          this.question.push({description:'',isSignup:true,ptype:0,id:this.index,title:'多选报名',answer:'',Answer:[],limit:2,numLimit:['1','2','3','4'],choices:['A','B','C','D'],necessary:false,type:'多选报名题'})
          this.index=this.index+1
        }
        else if(val.indexOf('学历')!=-1){
          this.question.push({description:'',isSignup:false,ptype:-1,id:this.index,title:'您的学历',limit:-1,answer:'',numLimit:['无'],choices:['小学','初中','高中','本科','硕士','博士'],necessary:false,type:'下拉框'})
          this.index=this.index+1
        }
        else if(val.indexOf('生日')!=-1){
          this.question.push({description:'',isSignup:false,menu:false,menu1:false,ptype:-1,date:'',time:'',id:this.index,title:'您的生日',limit:-1,answer:'',numLimit:['无'],choices:['无'],necessary:false,type:'时间选择器'})
          this.index=this.index+1
        }
        else if(val.indexOf('下拉框')!=-1){
          this.question.push({description:'',isSignup:false,ptype:-1,id:this.index,title:'标题',limit:-1,answer:'',numLimit:['无'],choices:['选项1','选项2','选项3','选项4'],necessary:false,type:'下拉框'})
          this.index=this.index+1
        }
        else if(val.indexOf('时间选择器')!=-1){
          this.question.push({description:'',isSignup:false,menu:false,menu1:false,ptype:-1,date:'',time:'',id:this.index,title:'标题',limit:-1,answer:'',numLimit:['无'],choices:['无'],necessary:false,type:'时间选择器'})
          this.index=this.index+1
        }
        console.log(this.question);
    }
  },
  mounted(){
    this.id=this.$route.params.id
    this.title=this.$route.query.title
    this.endTime=this.$route.query.endTime
    if(this.$route.query.isonetime!=undefined){
      if(this.$route.query.isonetime === 'true'){
        this.isOneTime = true;
      }
      else{
        this.isOneTime = false;
      }
    }
    if(this.$route.query.showseq!=undefined)this.showSeq = this.$route.query.showseq;
    if(this.$route.query.type==0)this.question=JSON.parse(this.$route.query.question);
    if(this.$route.query.date!=undefined&&this.id==-1) this.date = this.$route.query.date;
    if(this.$route.query.time!=undefined&&this.id==-1) this.time = this.$route.query.time;
    if(this.$route.query.paperDescription!=undefined) this.paperDescription=this.$route.query.paperDescription;
    // console.log(this.$route.query.title);
    console.log(this.$route.params.id,1111);
    if(this.id!=-1) {
      this.getData()
    }
  },
  data(){
    return {
      tip:false,
      index:-1000,
      id:-1,
      paperDescription:'',
      desdialog:false,
      title:'',
      date:'',
      menu:'',
      menu1:'',
      time:'',
      limits:[],
      endTime:'',
      showSeq:true,
      isEdit:false,
      editIndex:-1,
      overlay: false,
      choices:'',
      isOneTime:false,
      editQuestion:{id:this.index,title:'标题',answer:'',necessary:false,type:'填空题'},
      question:[
      ],
      titleList: [{id: 1, title: "个人信息", questionList: [{id: 1, question: "姓名"},{id:2,question: "性别"},{id: 3, question: "电话"},
                  {id: 4, question: "学历"},{id: 5, question: "生日"},{id: 6, question: "个人简介"},{id: 7, question: "报名理由"}]},
      {id: 2, title: "选择题", questionList: [{id: 1, question: "单选题"}, {id: 2, question: "多选题"},{id: 3, question: "下拉框"},{id: 4, question: "时间选择器"},
      {id: 5, question: "单选报名题"}, {id: 6, question: "多选报名题"}]},
      {id: 3, title: "文本填空题", questionList: [{id: 1, question: "填空题"}, {id: 2, question: "文本题"}]}],

    }
  },
  methods:{
    allowedDates: val => (parseInt(val.split('-')[2], 10)>=new Date().getDate()&&parseInt(val.split('-')[1], 10) ===new Date().getMonth()+1)
    ||parseInt(val.split('-')[0], 10) >new Date().getFullYear()||parseInt(val.split('-')[1], 10) >new Date().getMonth()+1&&val<this.endDate,
    getTime(){
      this.$refs.menu.save(this.time)
      console.log(this.date,this.time);
      return this.time
    },
    close(i){
      this.question.splice(i,1)
      this.index=this.index-1
    },
    copy(i){
      var newques={description:this.question[i].description,id:this.question[i].id,title:this.question[i].title,answer:'',necessary:this.question[i].necessary,type:this.question[i].type,choices:[],numLimit:[],limit:this.question[i].limit}
      this.index=this.index+1
      newques.id=this.index
      for(var j=0;j<this.question[i].choices.length;j++) {
        newques.choices.push(this.question[i].choices[j])
        newques.numLimit.push(this.question[i].numLimit[j])
      }
      console.log(newques,this.question[i]);
      this.question.splice(i,0,newques)
    },
    up(i){
      if(i==0) {
        this.$message.warning( "这是第一个题目，无法上移" );
        return
      }
      swapItems(this.question, i, i - 1);
    },
    down(i){
      var tmp=this.question[i]
      if(i==this.question.length-1) {
        this.$message.warning( "这是最后一个题目，无法下移" );
        return
        }
      swapItems(this.question, i, i + 1);
    },
    upp(i){
      if(i==0) {
        this.$message.warning( "这是第一个题目，无法上移" );
        return
      }
      swapItems(this.question, i, i - 1,'up');
    },
    downn(i){
      var tmp=this.question[i]
      if(i==this.question.length-1) {
        this.$message.warning( "这是最后一个题目，无法下移" );
        return
        }
      swapItems(this.question, i, i + 1,'down');
    },
    edit(i){
      this.editIndex=i
      this.editQuestion.title=this.question[i].title
      this.editQuestion.description=this.question[i].description
      this.editQuestion.necessary=String(this.question[i].necessary)
      this.editQuestion.isSignup=String(this.question[i].isSignup)
      this.editQuestion.choices=this.question[i].choices
      this.editQuestion.numLimit=this.question[i].numLimit
      this.editQuestion.type=this.question[i].type
      this.editQuestion.ptype=String(this.question[i].ptype)
      this.editQuestion.copychoices=[]
      this.editQuestion.copynumLimit=[]
      this.editQuestion.limit=this.question[i].limit
      for(var k=1;k<=this.question[i].choices.length;k++) this.limits.push(k)
      for(var j=0;j<this.question[i].choices.length;j++) {
        this.editQuestion.copychoices.push(this.question[i].choices[j])
        this.editQuestion.copynumLimit.push(this.question[i].numLimit[j])
      }
      this.editQuestion.type=this.question[i].type
      console.log(this.editQuestion);
      this.isEdit=true
    },
    cancelEdit(){
      this.question[this.editIndex].choices=this.editQuestion.copychoices
      this.question[this.editIndex].numLimit=this.editQuestion.copynumLimit
      this.isEdit=false
    },
    confirmEdit(){
      for(var i=0;i<this.editQuestion.numLimit.length;i++){
        var isnum = /^[123456789][0123456789]*$/.test(this.editQuestion.numLimit[i]);
        if(isnum==false&&this.editQuestion.numLimit[i]!='无'&&parseInt(this.editQuestion.numLimit[i])!=0){
          this.$message.error('请输入数字')
          return
        }
      }
      this.isEdit=false
      this.question[this.editIndex].title=this.editQuestion.title
      this.question[this.editIndex].description=this.editQuestion.description
      this.question[this.editIndex].choices=this.editQuestion.choices
      this.question[this.editIndex].numLimit=this.editQuestion.numLimit
      this.question[this.editIndex].limit=this.editQuestion.limit
      this.question[this.editIndex].type=this.editQuestion.type
      this.question[this.editIndex].ptype=parseInt(this.editQuestion.ptype)
      this.question[this.editIndex].necessary=eval(this.editQuestion.necessary)
      this.question[this.editIndex].isSignup=eval(this.editQuestion.isSignup)
      console.log(this.question[this.editIndex]);
      this.editIndex=-1
      this.editQuestion={}
    },
    deleteChoice(i){
      this.editQuestion.choices.splice(i,1)
      this.editQuestion.numLimit.splice(i,1)
      this.limits=[]
      for(var k=1;k<=this.editQuestion.choices.length;k++) this.limits.push(k)
      console.log(this.editQuestion.choices);
    },
    addChoice(){
      this.editQuestion.choices.push('')
      this.editQuestion.numLimit.push(0)
      this.limits=[]
      for(var k=1;k<=this.editQuestion.choices.length;k++) this.limits.push(k)
      console.log(this.editQuestion.choices);
    },
    deletePaper(){
      this.tip=true
    },
    deleteConfirm(){
      this.question=[]
      this.tip=false
    },
    deleteCancel(){
      this.tip=false
    },
    preview(){
      this.$router.push({path:'/preview',query:{url:'/signup/questionnare/'+this.id,showseq:this.showSeq,paperDescription:this.paperDescription,id:this.id,title:this.title,question:JSON.stringify(this.question),time: this.time, date:this.date, isonetime:this.isOneTime}})
    },
    a(){
      this.$router.push({path:'/answer',query:{url:'/signup/questionnare/'+this.id,id:this.id,title:this.title,question:JSON.stringify(this.question)}})
    },
    issue(){
      if(this.question.length==0){
        this.$message.warning('请至少添加一道题目')
        return
      }
      else if(this.endTime==undefined||this.endTime==''){
        this.$message.warning('请选择结束时间')
        return
      }
      for(var i=0;i<this.question.length;i++){
        if(this.question[i].type.indexOf('报名')!=-1){
          for(var j=0;j<this.question[i].numLimit.length;j++){
            this.question[i].numLimit[j]=parseInt(this.question[i].numLimit[j])
          }
        }
      }
      //for(var i=0;i<this.question.length;i++) this.question[i].choices=JSON.stringify(this.question[i].choices)
      console.log(this.question);
      this.$axios({url:'/sign/issue',method:'post',data:{
        endTime:this.endTime,
        paperType:'报名',
        user_id: parseInt(localStorage.getItem("id")),
        content:this.question,
        status:1,
        title:this.title,
        paperDescription:this.paperDescription,
        id:this.id,
        isSingle: this.isOneTime,
        showSeq:eval(this.showSeq)
      }})
        .then((res) => {
          if(res.data.status==true) {
            this.$message.success("发布成功");
            this.$router.push('/manage')
          }
          else this.$message.error({ type: "error", message: res.data.exec });
        })
        .catch((err) => {
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        });
    },
    save(){
      console.log(this.$store.state.user_id);
      if(this.question.length==0){
        this.$message.warning('请至少添加一道题目')
        return
      }
      else if(this.endTime==undefined||this.endTime==''){
        this.$message.warning('请选择结束时间')
        return
      }
      for(var i=0;i<this.question.length;i++){
        if(this.question[i].type.indexOf('报名')!=-1){
          for(var j=0;j<this.question[i].numLimit.length;j++){
            this.question[i].numLimit[j]=parseInt(this.question[i].numLimit[j])
          }
        }
      }
      //for(var i=0;i<this.question.length;i++) this.question[i].choices=JSON.stringify(this.question[i].choices)
      this.$axios({url:'/sign/issue',method:'post',data:{
        endTime:this.endTime,
        paperType:'报名',
        user_id: parseInt(localStorage.getItem("id")),
        content:this.question,
        title:this.title,
        paperDescription:this.paperDescription,
        status:0,
        id:this.id,
        isSingle: this.isOneTime,
        showSeq:eval(this.showSeq)
      }})
        .then((res) => {
          if(res.data.status==true) {
            this.$message.success("保存成功");
            this.$router.push('/manage')
          }
          else this.$message.error({ type: "error", message: res.data.exec });
        })
        .catch((err) => {
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        });
    },
    getData(){
      this.$axios({url:'/sign/content',method:'get',params:{
        id:this.id
      }})
        .then((res) => {
          console.log(res);
          if(res.data.status==false)  this.$message.error({ type: "error", message: res.data.exec });
          else {
            for(var i=0;i<res.data.content.length;i++){
              res.data.content[i].date=''
              res.data.content[i].time=''
              res.data.content[i].Answer=[]
              res.data.content[i].menu=false
              res.data.content[i].menu1=false
            }
            var array=new Array()
            array=res.data.endTime.split('')
            for(var i=0;i<10;i++)   this.date+=array[i]
            for(var i=11;i<16;i++)   this.time+=array[i]
            console.log(this.date,this.time);
            this.question=res.data.content
            this.title=res.data.title
            this.showSeq=res.data.showSeq
            this.isOneTime=res.data.isSingle
            this.paperDescription=res.data.paperDescription
          }
        })
        .catch((err) => {
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        });
    },
    canceldis(){
      this.overlay = false;
      this.paperDescription = "";
    },
    jumpToCreate() {
      this.$router.push('/questionnare');
    }
  }
}
</script>

